<template>
  <span
    v-if="Array.isArray(showList) && (showList.length === 0)"
  >
    没有选中的模块
  </span>
  <ul class="selected-modal-list" v-else>
    <li
      v-for="modal in showList"
      :key="modal[0].id"
      class="custom-li"
    >
      <span class="width-200 font-weight-bold">
        <i class="el-icon-folder-add"></i>
        {{ modal[0].menuName }}
      </span>
      <ul
        v-if="Array.isArray(modal[1]) && (modal[1].length > 0)"
        class="sub-modal-list"
      >
        <div class="sub-modal-list-item" v-for="child in modal[1]" :key="child.id"  >
          <table class="sub-modal-list-table" >
            <tr>
              <td class="sub-modal-list-itemName" >{{ child.menuName }}</td>
              <td class="sub-modal-list-itemBtn" >
                <p v-if="child.hasButton.length==0" >未配置页面按钮权限</p>
                <el-checkbox v-for="btn in child.hasButton" :key="btn.id" v-model="btn.isChecked">
              {{ btn.buttonName }}({{btn.cssTypeDesc}})
              </el-checkbox>
              </td>
            </tr>
          </table>
        </div>
      </ul>
      <span class="span--no-sub-modal" v-else>没有子模块</span>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'SelectedModalList',

  props: {
    selectedModals: {
      type: Array,
      default: () => []
    }
  },

  data() {
    return {
      showList: []
    };
  },

  watch: {
    selectedModals: {
      handler: function(val, oldVal) {
        this.showList = val;
      },
      immediate: true,
      deep: true
    }
  },

  updated() {
  }
};
</script>

