<template>
  <el-container>
    <el-header>
      <!-- <header-title :titleName="pageTitle" :flag="false"></header-title> -->
      <header-buttons
        @buttonClickEvent="buttonClickEvent"
        cssType="99"
        showBack="true"
        showTitle="true"
        titleName="权限设置"
      >
      </header-buttons>
    </el-header>
    <el-main class="border-top">
      <section class="display-grid margin-10 authority-setting-area">
        <el-card shadow="never" class="custom-card">
          <div slot="header" class="custom-card__slot">
            <span class="tree-title">模块</span>
          </div>
          <div class="scroll-area">
            <el-scrollbar style="height: 100%">
              <!-- 树组件 -->
              <!-- <el-tree
                class="modal-tree"
                ref="modalTree"
                node-key="id"
                :data="treeData"
                :props="defaultProps"
                @node-click="handleNodeClickEvent"
                @check="handleNodeCheckEvent"
                :render-content="renderContent"
                check-strictly
                default-expand-all
                show-checkbox
              ></el-tree>-->
              <ZTree
                :nodes="treeData"
                :isCheck="true"
                :isExpendAll="true"
                @ztreeOnCheck="ztreeOnCheck"
                @ztreeHandleCreated="ztreeHandleCreated"
              ></ZTree>
            </el-scrollbar>
          </div>
        </el-card>
        <el-card shadow="never" class="custom-card">
          <div slot="header" class="custom-card__slot">
            <span class="tree-title">方法</span>
          </div>
          <div class="scroll-area">
            <el-scrollbar style="height: 100%">
              <selected-modal-list
                :selected-modals="selectedModals"
              ></selected-modal-list>
            </el-scrollbar>
          </div>
        </el-card>
      </section>
      <article style="text-align: center">
        <el-button class="custom-button" @click="goBack">返回</el-button>
        <el-button
          type="primary"
          class="custom-button"
          @click="saveAuthoritiesForRole"
          >保存</el-button
        >
      </article>
    </el-main>
  </el-container>
</template>

<script>
// import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import SelectedModalList from '@/views/main/RoleManagement/SelectedModalList';
// 引入ztree树形组件
import ZTree from '@/components/common/ZTree/ZTree';
import { showErrorMessage, showSuccessMessage } from '@/utils/message';
// import { getroleGroupTree, saveAuthorities } from '@/api/master';
// 导入所需接口
import {
  powerGetListPowerAction,
  rolePowerRelationshipGetListPowerByRoleIdAction,
  rolePowerRelationshipUpdateRoleRelationshipPowerAction,
  rolePowerRelationshipUpdateRoleButtonRelationshipAction
} from '@/api/role';
export default {
  name: 'PageAuthoritySetting',

  components: {
    // HeaderTitle,
    SelectedModalList,
    ZTree
  },

  props: {
    // 从父页面传递来的角色id
    roleId: {
      type: Number,
      default: () => 0,
      required: true
    }
  },

  data() {
    return {
      pageTitle: '权限设置',

      // 树组件数据
      firstInit: 0,
      treeData: [],
      ztreeObj: {},
      defaultProps: {
        children: 'children',
        label: 'menuName'
      },
      roleTreeId: 0, // 暂存权限节点ID
      selectedModals: [],
      selectedBtns: [],
      releBtn: {}
    };
  },

  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      if (type === 'exit') {
        this.goBack();
        return;
      }
      this[`${type}`]();
    },
    /**
     * @description: 获取左侧树组件的数据
     * @param 无
     * @return: void
     * @author: cyb
     */
    async fetchTreeData() {
      try {
        // const conditionJson = {
        //   roleId: this.roleId
        // };
        const res = await powerGetListPowerAction();

        // // 是否为管理元
        // if (this.roleId === 1) {
        //   var index = 0;
        //   for (var x in res.rows) {
        //     if (res.rows[x].powerEnName === 'RoleManagement') {
        //       index = x;
        //     }
        //   }
        //   if (index > 0) {
        //     res.rows.splice(index, 1);
        //   }
        // }

        this.treeData = this.dataFormatterTree(res.rows);
        // this.treeData = res.rows;
        this.initSelectedModals();
      } catch (error) {
        // showErrorMessage(error);
      }
    },
    /**
     * @description: 返回数据格式化
     * @param data rows数据
     * @return: 格式化后数据
     * @author: ZJM
     */
    dataFormatterTree(data) {
      var treeArray = data;
      for (var x in treeArray) {
        treeArray[x].id = treeArray[x].menuId;
        treeArray[x].name = treeArray[x].powerCnName;
        treeArray[x].pId = treeArray[x].menuParentId;
      }
      var r = [];
      var tmpMap = {};
      for (var i = 0, l = treeArray.length; i < l; i++) {
        tmpMap[treeArray[i].id] = treeArray[i];
      }
      for (i = 0, l = treeArray.length; i < l; i++) {
        var key = tmpMap[treeArray[i].pId];
        if (key) {
          if (!key.children) {
            key.children = [];
            delete treeArray[i].url;
            key.children.push(treeArray[i]);
          } else {
            delete treeArray[i].url;
            key.children.push(treeArray[i]);
          }
        } else {
          r.push(treeArray[i]);
        }
      }
      return r;
    },
    // renderContent(h, { node, data }) {
    //   if (data.hasflag) {
    //     node.checked = true;
    //   }

    //   return <span>{node.label}</span>;
    // },

    /**
     * @description: 找到某个父节点是否位于selectedModals中
     * @param {number} id 父节点的id
     * @return: boolean
     * @author: cyb
     */
    isParentNodeInArray(id) {
      return this.selectedModals.findIndex(modal => modal[0].id === id) !== -1;
    },

    /**
     * @description: 页面初始化的时候根据
     * @param 无
     * @return: void
     * @author: cyb
     */
    async initSelectedModals() {
      if (this.firstInit === 0) {
        this.firstInit = 1;
        try {
          // const conditionJson = {
          //   roleId: this.roleId
          // };
          const res = await rolePowerRelationshipGetListPowerByRoleIdAction({
            roleId: this.roleId
          });
          this.releBtn = res;
          console.log(res.rows);
          for (var item in res.rows) {
            // console.log(res.rows[item]);
            // var node = this.ztreeObj.getNodeByParam(
            //   'id',
            //   res.rows[item].menuId,
            //   null
            // );
            // this.ztreeObj.checkNode(node, true, true);
            if (res.rows[item].menuParentId !== -1) {
              var node = this.ztreeObj.getNodeByParam(
                'id',
                res.rows[item].menuId,
                null
              );
              this.ztreeObj.checkNode(node, true, true);
            // } else if (res.rows[item].powerEnName === 'MainControlRoom') {
            //   var nodex = this.ztreeObj.getNodeByParam(
            //     'id',
            //     res.rows[item].menuId,
            //     null
            //   );
            //   this.ztreeObj.checkNode(nodex, true, true);
            // }
            } else {
              var flag = 0;
              for (var itemx in res.rows) {
                if (res.rows[itemx].menuParentId === res.rows[item].menuId) {
                  flag++;
                }
              }
              console.log(res.rows[item].powerCnName, flag);
              if (flag === 0) {
                var nodexx = this.ztreeObj.getNodeByParam(
                  'id',
                  res.rows[item].menuId,
                  null
                );
                this.ztreeObj.checkNode(nodexx, true, true);
              }
            }
          }
          // this.treeData = this.dataFormatterTree(res.rows);
          // this.initSelectedModals();
        } catch (error) {
          // showErrorMessage(error);
        }
      }

      this.selectedModals.splice(0);
      var treeData = this.ztreeObj.getNodes();
      if (treeData.length > 0) {
        treeData.forEach(node => {
          // hasflag为true代表当前父节点为选中状态
          if (node.checked) {
            const modal = [];
            // 先加入父节点的数据

            var arr = [];
            // if (node.hasButton && node.hasButton.length > 0) {
            //   // node.hasButton.forEach(item => {
            //   //   item.isChecked = false;
            //   //   arr.push(item);
            //   // });
            //   node.hasButton.forEach(item => {
            //     var resList = this.releBtn.rows;
            //     for (var i = 0; i < resList.length; i++) {
            //       if (node.menuId === resList[i].menuId) {
            //         if (resList[i].hasButton && resList[i].hasButton.length > 0) {
            //           var resListBtn = resList[i].hasButton;
            //           for (var j = 0; j < resListBtn.length; j++) {
            //             if (resListBtn[j].id === item.id) {
            //               item.isChecked = true;
            //             }
            //           }
            //         } else {
            //           item.isChecked = false;
            //         }
            //       }
            //     }
            //     arr.push(item);
            //   });
            // }

            modal.push({
              id: node.id,
              menuName: node.name,
              parentId: -1,
              powerId: node.powerId,
              hasButton: arr
            });

            // 如果存在多个选中的子节点
            if (node.children && node.children.length > 0) {
              const checkedChildren = [];
              node.children
                .filter(child => child.checked === true)
                .forEach(child => {
                  var arr = [];
                  if (child.hasButton.length > 0) {
                    child.hasButton.forEach(item => {
                      var resList = this.releBtn.rows;
                      if (resList && resList.length > 0) {
                        for (var i = 0; i < resList.length; i++) {
                          if (child.menuId === resList[i].menuId) {
                            var resListBtn = resList[i].hasButton;
                            // 第二步确定是哪个按钮
                            for (var j = 0; j < resListBtn.length; j++) {
                              if (resListBtn[j].id === item.id) {
                                if (item.isChecked !== false) {
                                  item.isChecked = true;
                                } else {
                                  item.isChecked = false;
                                }
                              }
                            }
                          }
                        }
                      }
                      arr.push(item);
                    });
                  }
                  const finalChild = {
                    id: child.id,
                    menuName: child.name,
                    parentId: child.pId,
                    powerId: child.powerId,
                    hasButton: arr
                  };
                  checkedChildren.push(finalChild);
                });
              modal.push(checkedChildren);
            } else {
              modal.push([]);
            }
            this.firstInit = 2;
            this.selectedModals.push(modal);
          }
        });
      }
    },

    /**
     * @description: “保存”按钮点击回调函数
     * @param {}
     * @return:
     * @author: Fizz
     */
    async saveAuthoritiesForRole() {
      try {
        const res = await rolePowerRelationshipUpdateRoleRelationshipPowerAction(this.constructAuthArray());
        if (res) {
          // this.$emit('refreshParentMenu');
          showSuccessMessage('权限设置成功！');
          this.saveSelectBtn();
        }
      } catch (error) {
        showErrorMessage(error);
      }
    },
    async saveSelectBtn() {
      try {
        const res = await rolePowerRelationshipUpdateRoleButtonRelationshipAction({
          roleId: this.roleId
        }, this.selectedBtns);
        if (res) {
          // this.$emit('refreshParentMenu');
          showSuccessMessage('权限角色按钮设置成功！');
        }
      } catch (error) {
        showErrorMessage(error);
      }
    },
    /**
     * @description: 构造保存权限接口能接受的参数
     * @param 无
     * @return: array
     * @author: cyb
     */
    constructAuthArray() {
      this.selectedBtns = [];
      const arr = {
        roleId: this.roleId,
        powerIdList: []
      };

      for (const [parent, children] of this.selectedModals) {
        // 保存父节点的id
        arr.powerIdList.push(parent.powerId);

        if (parent.hasButton && parent.hasButton.length > 0) {
          parent.hasButton.forEach(item => {
            if (item.isChecked) {
              var json = {
                buttonId: item.id,
                powerId: item.powerId,
                roleId: this.roleId
              };
              this.selectedBtns.push(json);
            }
          });
        }

        // 保存子节点的id
        if (children.length > 0) {
          children.forEach(child => {
            arr.powerIdList.push(child.powerId);

            if (child.hasButton && child.hasButton.length > 0) {
              child.hasButton.forEach(item => {
                if (item.isChecked) {
                  var json = {
                    buttonId: item.id,
                    powerId: item.powerId,
                    roleId: this.roleId
                  };
                  this.selectedBtns.push(json);
                }
              });
            }
          });
        }
      }
      return arr;
    },

    /**
     * @description: “返回”按钮点击回调函数
     * @param {}
     * @return:
     * @author: Fizz
     */
    goBack() {
      this.$emit('goBack', 0);
    },
    /**
     * @description: ztree初始化完成事件
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    ztreeHandleCreated(obj) {
      // 获取ztree对象
      this.ztreeObj = obj;
    },
    ztreeOnCheck(event, nodes, obj) {
      console.log(nodes);
      if (nodes.menuParentId === -1) {
        if (nodes.children && nodes.children.length > 0) {
          for (var y = 0; y < nodes.children.length; y++) {
            var nodex = nodes.children[y];
            if (nodex.hasButton && nodex.hasButton.length > 0) {
              var flagx = -1;
              for (var x1 = 0; x1 < this.releBtn.rows.length; x1++) {
                // 如果有说明保存过
                if (nodex.menuId === this.releBtn.rows[x1].menuId) {
                  flagx = 0;
                  // 验证是否保存过按钮
                  if (!this.releBtn.rows[x1].hasButton || this.releBtn.rows[x1].hasButton.length === 0) {
                    flagx = 1;
                  } else {
                    flagx = 2;
                  }
                }
              }
              // 不等于2，说明按钮没有保存或菜单没有保存过，可以放心遍历
              if (flagx !== 2) {
                for (var i1 = 0; i1 < nodex.hasButton.length; i1++) {
                  if (nodex.hasButton[i1].isChecked === false) {
                    nodex.hasButton[i1].isChecked = false;
                  } else {
                    nodex.hasButton[i1].isChecked = true;
                  }
                }
              }
            }
          }
        }
      } else {
        if (nodes.hasButton && nodes.hasButton.length > 0) {
          var flag = -1;
          for (var x = 0; x < this.releBtn.rows.length; x++) {
            // 如果有说明保存过
            if (nodes.menuId === this.releBtn.rows[x].menuId) {
              flag = 0;
              // 验证是否保存过按钮
              if (!this.releBtn.rows[x].hasButton || this.releBtn.rows[x].hasButton.length === 0) {
                flag = 1;
              } else {
                flag = 2;
              }
            }
          }
          // 不等于2，说明按钮没有保存或菜单没有保存过，可以放心遍历
          if (flag !== 2) {
            for (var i = 0; i < nodes.hasButton.length; i++) {
              if (nodes.hasButton[i].isChecked === false) {
                nodes.hasButton[i].isChecked = false;
              } else {
                nodes.hasButton[i].isChecked = true;
              }
            }
          }
        }
      }
      this.initSelectedModals();
    }
  },

  created() {
    this.fetchTreeData();
  }
};
</script>

<style lang="less" scoped>
.border-top {
  border-top: 1px solid #dedddd;
}

.display-grid {
  display: grid;
  grid-template-columns: 260px 1fr;
  grid-template-rows: calc(100vh - 210px);
  column-gap: 10px;
}

.margin-10 {
  margin: 10px;
}

// 卡片自定义样式
.authority-setting-area /deep/ .el-card {
  border-radius: 0;
}

// 卡片头部slot自定义样式
.custom-card /deep/ .el-card__header {
  padding: 10px;
}

// 卡片body自定义样式
.custom-card /deep/ .el-card__body {
  padding: 10px;
}

.custom-card__slot {
  display: flex;
}

// 卡片标题样式
.tree-title {
  display: inline-block;
  padding-left: 10px;
  font-weight: bold;
  border-left: 3px solid #0871dc;
  line-height: 20px;
}

.scroll-area {
  height: calc(100vh - 270px);
}

.scroll-area /deep/ .el-scrollbar__wrap {
  overflow-x: hidden;
}

// 隐藏非叶子节点，因为非叶子节点前的checkbox会影响子模块的勾选和取消勾选事件的正确性
.modal-tree /deep/ .el-tree-node {
  .is-leaf + .el-checkbox .el-checkbox__inner {
    display: inline-block;
  }
  .el-checkbox__input > .el-checkbox__inner {
    display: none;
  }
}
</style>
